.banner {
  background: url("https://media.cntraveler.com/photos/5db1d0dd11c1e500092e7133/master/pass/airbnb-ski-aspen-28328347.jpg")
    center center;
  height: 50vh;
  position: relative;
}

.banner__info {
  background-color: black;
  color: white; /*to make the text color white*/
  padding-top: 25vh;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 40px;
  width: 300px;
}

.banner__search {
  display: flex;
  flex-direction: column;
}

/*whenever we give material ui a class we have to use the !important */
.banner__searchButton {
  background-color: white !important;
  font-weight: 900 !important;
  text-transform: inherit !important;
  color: #ff7779 !important;
}

.banner__info > button {
  background-color: #ff7779;
  color: white;
  text-transform: inherit; /*to prevent everything becoming uppercase bcz of default material ui*/
  margin-top: 20px;
  font-weight: 600;
}

.banner__info > button:hover {
  background-color: #fff;
  color: #ff7779;
}

.banner__info > h5 {
  margin-top: 10px;
}
